import React, { useState, useEffect } from 'react'
import IngresoDatos from './IngresoDatos';
import MiLecheria from "../Media/MiLecheria.jpg"

function Presentacion() {

    const [mostrarInstrucciones, setMostrarInstrucciones] = useState(true);
    const [comenzar, setComenzar] = useState(false);

    const manejarOnClick1 = () => {
        setMostrarInstrucciones(!mostrarInstrucciones);
    }

    const manejarOnclick2 = () => {
        setComenzar(true);
    }
    return (
        <div>
            <header className="App-header">
                <img src={MiLecheria} id="MiLecheria"></img>
                <h1>CRECIMIENTO DEL RODEO LECHERO</h1>
            </header>
            {mostrarInstrucciones && (<div className="seccion">
                <h2>Instrucciones:</h2>
                <div id="instrucciones">
                    <p>Durante un perodo de tiempo determinado (normalmente se toma un ao) se considera que un rodeo lechero crece cuando el nmero de vacas adultas que finalizan su vida til (abandonan el rodeo por muerte y/o por rechazo) es menor que el nmero de vacas nuevas (vaquillonas de reemplazo) que inician su vida productiva, verificndose que al final del perodo el rodeo de vacas adultas tiene ms cabezas que al comienzo.</p>
                    <p>Lo anterior debera lograrse como resultado de la dinmica propia del rodeo (crecimiento genuino o crecimiento interno del rodeo) sin necesidad de recurrir a la compra de vacas y/o vaquillonas. El crecimiento genuino es una estrategia de efecto consistente, sostenido e infinito en el tiempo que depende de la buena planificacin y ejecucin eficiente de los manejos del rodeo.</p>
                    <p>Mediante el uso de esta aplicacin web Usted podr estimar la tasa anual de crecimiento genuino de un rodeo lechero a partir del ingreso de algunos parmetros de rodeo.</p>
                    <p>Partiendo de un rodeo base Usted podr evaluar los impactos en el crecimiento de rodeo que produce cada parmetro. La aplicacin le permitir evaluar tres sets de parmetros (Estados) en forma comparada. Por ejemplo, situacin real versus otras dos situaciones alternativas.</p>
                    <p>Los requerimientos de ingreso de parmetros estn divididos en dos formularios: "Clculo de bajas anuales y vida til de vacas adultas" y "Clculo de reposicin anual efectiva". Debe completarse el primero para acceder al segundo. Cada uno de ellos presenta los resultados parciales al pie al finalizar el llenado.</p>
                    <p>Haciendo click sobre la etiqueta de cada parmetro a ingresar se muestra una definicin simplificada o un breve mensaje de ayuda.</p>
                    <p>Resultados que se obtienen:</p>
                    <ul>
                        <li>Grfico comparando bajas con reposicin de corto y largo plazo</li>
                        <li>Relacin Reemplazos / Bajas (corto y largo plazo)</li>
                        <li>Crecimiento porcentual del rodeo (corto y largo plazo)</li>
                        <li>Grfico de evolucin comparada a cinco aos (para tres estados diferentes del rodeo y partiendo del nmero de vacas elegido por el usuario)</li>
                    </ul>
                    <p>En el grfico de evolucin comparada los tres estados toman por defecto los valores de la primera simulacin finalizada. A partir de all se pueden hacer todos los cambios que el usuario desee. Cualquier situacin simulada puede guardarse en cualquiera de los estados y ver su curva de evolucin en el grfico.</p>
                    <p>Una vez guardadas las simulaciones en los tres estados el usuario podr generar un reporte en formato PDF ingresando algunos datos adicionales incluido un email vlido al que ser enviado dicho reporte.</p>
                
                
                </div>
            </div>)}
            <div>
                <button onClick={manejarOnClick1}>
                    {mostrarInstrucciones === true ? "Ocultar instrucciones" : "Mostrar instrucciones"}
                </button>
                {comenzar === false && (<button onClick={manejarOnclick2}>
                    Aceptar trminos y comenzar
                </button>)}
            </div>
            {comenzar && (<div>
                <IngresoDatos />
            </div>)}
        </div>
    )
}

export default Presentacion